@use './mixin' as *;

.text-up {
  text-transform: uppercase;
}

.date {
  color: var(--clr-blue-50);
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: auto;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
  transition: all var(--transition);
}
